    .lg-item.lg-complete .lg-object {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        opacity: 1;
    }
}

.lg-outer {
    .lg-thumb-outer {
        background-color: #121212;
        height: 100% !important;
        position: absolute;
        width: 80px;
        right: 60px;
        z-index: 1080;
        border-left: 1px solid rgba(255, 255, 255, 0.1);
        will-change: transform;
        -webkit-transition: .4s;
        transition: .4s;
        -webkit-transform: translate(100%);
        -ms-transform: translate(100%);
        transform: translate(100%);

        &.show {
            -webkit-transform: translate(0);
            -ms-transform: translate(0);
            transform: translate(0);
        }

        &.lg-grab .lg-thumb-item {
            cursor: -webkit-grab;
            cursor: -o-grab;
            cursor: -ms-grab;
            cursor: grab;
        }

        &.lg-grabbing .lg-thumb-item {
            cursor: move;
            cursor: -webkit-grabbing;
            cursor: -o-grabbing;
            cursor: -ms-grabbing;
            cursor: grabbing;
        }
    }

    .lg-thumb {
        height: 100%;
        width: 80px !important;
        -webkit-transform: none !important;
        -ms-transform: none !important;
        transform: none !important;
        padding: 40px 4px;
    }

    .lg-thumb-item {
        position: relative;
        height: 45px;
        width: 100% !important;
        margin-bottom: 4px;
        opacity: 0.7;

        &.active {
            opacity: 1;

            &:before {
                content: "";
                background: #E5E5E5;
                height: 100%;
                width: 2px;
                position: absolute;
                left: -5px;
            }

            border-color: #a90707;
        }

        &:hover {
            border-color: #a90707;
        }

        img {
            width: 100%;
            height: 100%;
            -o-object-fit: contain;
            object-fit: contain;
            -o-object-position: top;
            object-position: top;
        }
    }

    &.lg-has-thumb .lg-item {
        padding-bottom: 120px;
    }

    &.lg-can-toggle .lg-item {
        padding-bottom: 0;
    }

    .lg-toggle-thumb {
        background-color: #0D0A0A;
        border-radius: 2px 2px 0 0;
        color: #999;
        cursor: pointer;
        font-size: 24px;
        height: 39px;
        line-height: 27px;
        padding: 5px 0;
        position: absolute;
        right: 20px;
        text-align: center;
        top: -39px;
        width: 50px;

        &:after {
            content: "";
        }

        &:hover {
            color: #FFF;
        }
    }

    .lg-video-cont {
        display: inline-block;
        vertical-align: middle;
        max-width: 1140px;
        max-height: 100%;
        width: 100%;
        padding: 0 5px;
    }

    .lg-video {
        width: 100%;
        height: 0;
        padding-bottom: 56.25%;
        overflow: hidden;
        position: relative;

        .lg-object {
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100% !important;
            height: 100% !important;
        }

        .lg-video-play {
            width: 84px;
            height: 59px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -42px;
            margin-top: -30px;
            z-index: 1080;
            cursor: pointer;
        }
    }

    .lg-has-vimeo {
        .lg-video-play {
            background: url("images/icons/light_gallery/vimeo-play.png") no-repeat scroll 0 0 transparent;
        }

        &:hover .lg-video-play {
            background: url("images/icons/light_gallery/vimeo-play.png") no-repeat scroll 0 -58px transparent;
        }
    }

    .lg-has-html5 {
        .lg-video-play {
            background: transparent url("images/icons/light_gallery/video-play.png") no-repeat scroll 0 0;
            height: 64px;
            margin-left: -32px;
            margin-top: -32px;
            width: 64px;
            opacity: 0.8;
        }

        &:hover .lg-video-play {
            opacity: 1;
        }
    }

    .lg-has-youtube {
        .lg-video-play {
            background: url("images/icons/light_gallery/youtube-play.png") no-repeat scroll 0 0 transparent;
        }

        &:hover .lg-video-play {
            background: url("images/icons/light_gallery/youtube-play.png") no-repeat scroll 0 -60px transparent;
        }
    }

    .lg-video-object {
        width: 100% !important;
        height: 100% !important;
        position: absolute;
        top: 0;
        left: 0;
    }

    .lg-has-video {
        .lg-video-object {
            visibility: hidden;
        }

        &.lg-video-playing {

            .lg-object,
            .lg-video-play {
                display: none;
            }

            .lg-video-object {
                visibility: visible;
            }
        }
    }
}

