.lg-outer {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    opacity: 0;
    -webkit-transition: opacity 0.15s ease 0s;
    transition: opacity 0.15s ease 0s;

    * {
        box-sizing: border-box;
    }

    &.lg-visible {
        opacity: 1;
    }

    &.lg-css3 {
        .lg-item {

            &.lg-prev-slide,
            &.lg-next-slide,
            &.lg-current {
                -webkit-transition-duration: inherit !important;
                transition-duration: inherit !important;
                -webkit-transition-timing-function: inherit !important;
                transition-timing-function: inherit !important;
            }
        }

        &.lg-dragging .lg-item {

            &.lg-prev-slide,
            &.lg-next-slide,
            &.lg-current {
                -webkit-transition-duration: 0s !important;
                transition-duration: 0s !important;
                opacity: 1;
            }
        }
    }

    &.lg-grab img.lg-object {
        cursor: -webkit-grab;
        cursor: -o-grab;
        cursor: -ms-grab;
        cursor: grab;
    }

    &.lg-grabbing img.lg-object {
        cursor: move;
        cursor: -webkit-grabbing;
        cursor: -o-grabbing;
        cursor: -ms-grabbing;
        cursor: grabbing;
    }

    .lg {
        height: 100%;
        width: 100%;
        position: relative;
        overflow: hidden;
        margin-left: auto;
        margin-right: auto;
        max-width: 100%;
        max-height: 100%;
    }

    .lg-inner {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        white-space: nowrap;
    }

    .lg-item {
        background: url("images/icons/loading.gif") no-repeat center transparent;
        background-size: 50px;
        display: none !important;
    }

    &.lg-css3 {

        .lg-prev-slide,
        .lg-current,
        .lg-next-slide {
            display: inline-block !important;
        }
    }

    &.lg-css .lg-current {
        display: inline-block !important;
    }

    .lg-item,
    .lg-img-wrap {
        display: inline-block;
        text-align: center;
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .lg-item:before {
        content: "";
        display: inline-block;
        height: 50%;
        width: 1px;
    }

    .lg-img-wrap {
        &:before {
            content: "";
            display: inline-block;
            height: 50%;
            width: 1px;
        }

        position: absolute;
        padding: 0 65px 0 5px;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        -webkit-transition-duration: 250ms;
        transition-duration: 250ms;
        -webkit-transition-property: padding !important;
        transition-property: padding !important;
        -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    }

    .lg-item {
        &.lg-complete {
            background-image: none;
        }

        &.lg-current {
            z-index: 1060;
        }
    }

    .lg-image {
        display: inline-block;
        vertical-align: middle;
        max-width: 100%;
        max-height: 100%;
        width: auto !important;
        height: auto !important;
    }

    &.lg-show-after-load .lg-item {

        .lg-object,
        .lg-video-play {
            opacity: 0;
            -webkit-transition: opacity 0.15s ease 0s;
            transition: opacity 0.15s ease 0s;
        }

        &.lg-complete {

            .lg-object,
            .lg-video-play {
                opacity: 1;
            }
        }
    }

    .lg-empty-html,
    &.lg-hide-download #lg-download {
        display: none;
    }
}

.lg-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1040;
    background-color: #121212;
    opacity: 0;
    -webkit-transition: opacity 0.15s ease 0s;
    transition: opacity 0.15s ease 0s;

    &.in {
        opacity: 1;
    }
}

.lg-css3 {
    &.lg-no-trans {

        .lg-prev-slide,
        .lg-next-slide,
        .lg-current {
            -webkit-transition: none 0s ease 0s !important;
            transition: none 0s ease 0s !important;
        }
    }

    &.lg-use-css3 .lg-item,
    &.lg-use-left .lg-item {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    }

    &.lg-fade .lg-item {
        opacity: 0;

        &.lg-current {
            opacity: 1;
        }

        &.lg-prev-slide,
        &.lg-next-slide,
        &.lg-current {
            -webkit-transition: opacity 0.1s ease 0s;
            transition: opacity 0.1s ease 0s;
        }
    }

    &.lg-slide {
        &.lg-use-css3 .lg-item {
            opacity: 0;

            &.lg-prev-slide {
                -webkit-transform: translate3d(-100%, 0, 0);
                transform: translate3d(-100%, 0, 0);
            }

            &.lg-next-slide {
                -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
            }

            &.lg-current {
                -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
                opacity: 1;
            }

            &.lg-prev-slide,
            &.lg-next-slide,
            &.lg-current {
                -webkit-transition: -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
                -webkit-transition: opacity 0.1s ease 0s, -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s;
                transition: opacity 0.1s ease 0s, -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s;
                transition: transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
                transition: transform 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s, -webkit-transform 1s cubic-bezier(0, 0, 0.25, 1) 0s;
            }
        }

        &.lg-use-left .lg-item {
            opacity: 0;
            position: absolute;
            left: 0;

            &.lg-prev-slide {
                left: -100%;
            }

            &.lg-next-slide {
                left: 100%;
            }

            &.lg-current {
                left: 0;
                opacity: 1;
            }

            &.lg-prev-slide,
            &.lg-next-slide,
            &.lg-current {
                -webkit-transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
                transition: left 1s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.1s ease 0s;
            }
        }
    }
}

